        data.oldQueryKeyWordReg = ''; //无内容搜索就设置空
        data.catalogue = []; //清空匹配定位
        // 重置表格复制
        setTimeout(() => {
          copyTable();
        }, 0);
      };
    };
    // 目录（1）
    const generateCatalog = (e) => {
      // 获取目录元素迭代器
      const fileCatalog = e.target.result.matchAll(
        /<div level="(.*?)" tagetid="(.*?)" catalogtitle="(.*?)" ?>/g
      );
      for (const Catalog of fileCatalog) {
        const CatalogObj = {
          recursion: parseInt(Catalog[1]), //用于递归次数
          grade: Catalog[1], //目录层级
          id: Catalog[2], //目录id  例如：tag_p_1
          text: Catalog[3], //目录内容
          children: [] //目录子级
        };
        // 递归生成目录
        recursionSetCatalog(data.longTextCatalog, CatalogObj);
      }
    };
    // 目录（2）递归生成目录（目标对象，要追加的对象）
    const recursionSetCatalog = (tarObj, addObj) => {
      // 控制层级，不是0就控制层级数量，0是全部
      if (data.catalogGrade) {
        if (addObj.recursion > data.catalogGrade) return; //控制几级目录
      }

      // 递归次数等于1就追加到目标对象中
      if (addObj.recursion === 1) {
        tarObj.push(addObj);
      } else {
        // 不等于1，就递归标记-- 进入下次循环
        addObj.recursion--;
        // 再次传入当前  （对象中最后一个元素的children，及要追加的对象）
        recursionSetCatalog(tarObj[tarObj.length - 1].children, addObj);
      }
    };
    // 生成目录6级深度(手动生成，已优化上面递归)
    /*    const generateCatalog = (e) => {
      // 获取目录元素迭代器
      const fileCatalog = e.target.result.matchAll(
