      let gradeNum1 = -1; //保存1级目录下标
      let gradeNum2; //保存2级级目录下标
      let gradeNum3; //保存3级级目录下标
      let gradeNum4; //保存4级级目录下标
      let gradeNum5; //保存5级级目录下标
      for (const Catalog of fileCatalog) {
        const CatalogObj = {
          grade: Catalog[1], //目录层级
          id: Catalog[2], //目录id
          text: Catalog[3], //目录内容
          children: [] //目录子级
        };
        if (Catalog[1] === '1') {
          //一级目录
          gradeNum1++;
          gradeNum2 = -1; //有一级出现就要重置二级对象索引
          data.longTextCatalog.push(CatalogObj);
        } else if (Catalog[1] === '2') {
          //二级目录
          gradeNum2++;
          gradeNum3 = -1;
          data.longTextCatalog[gradeNum1].children.push(CatalogObj);
        } else if (Catalog[1] === '3') {
          //三级目录
          gradeNum3++;
          gradeNum4 = -1;
          data.longTextCatalog[gradeNum1].children[gradeNum2].children.push(
            CatalogObj
          );
        } else if (Catalog[1] === '4') {
          //三级目录
          gradeNum4++;
          gradeNum5 = -1;
          data.longTextCatalog[gradeNum1].children[gradeNum2].children[
            gradeNum3
          ].children.push(CatalogObj);
        } else if (Catalog[1] === '5') {
          //三级目录
          gradeNum5++;
          data.longTextCatalog[gradeNum1].children[gradeNum2].children[
            gradeNum3
          ].children[gradeNum4].children.push(CatalogObj);
        } else if (Catalog[1] === '6') {
          //三级目录
          data.longTextCatalog[gradeNum1].children[gradeNum2].children[
            gradeNum3
          ].children[gradeNum4].children[gradeNum5].children.push(CatalogObj);
        }
      }
    }; */
    //（表格复制） 获取表格遍历添加复制按键，添加按键复制事件
    const copyTable = () => {
      // 全部table元素转化为真数组
      let tableEleAll = Array.from(document.getElementsByTagName('table'));
      // 遍历全部table元素
      tableEleAll.forEach((itemTable, index) => {
        // 移入表格创建复制按键，增加点击事件
        itemTable.onmouseenter = function (e) {
          data.copyButton = document.createElement('button'); //保存按键元素移除时使用
          data.copyButton.className = 'copy';
          data.copyButton.innerHTML = '复制表格';
          const currentEle = e.target; //要复制的表格元素
          currentEle.appendChild(data.copyButton); //复制按键添加到表格
          // 点击复制按键，设置当前表格为复制目标
          data.copyButton.onclick = new Clipboard('.copy', {
            // 通过target指定要复印的节点
            target: function () {
              //点击时清除复制按键，解决复制带有复制表格字样
              itemTable.removeChild(itemTable.lastChild);
              console.log(currentEle);
              // 复制时设置table属性宽度，解决复制到word中宽度超出
              currentEle.width = '98%';
              return currentEle;
            }
          });
          //复制成功
          data.copyButton.onclick.on('success', function (e) {
            emptySelectText();
            // 解决复制选中高亮（清除页面选中文字）
            myTips('success', '复制成功~');
            data.copyButton.onclick.destroy();
          });
          // 复制失败
          data.copyButton.onclick.on('error', function (e) {
            emptySelectText();
            // 解决复制选中高亮（清除页面选中文字）
            myTips('error', '复制失败~');
            data.copyButton.onclick.destroy();
          });
        };
        // 移除时清除按键事件和按键元素
        itemTable.onmouseleave = function () {
          data.copyButton.onclick.destroy();
          itemTable.removeChild(itemTable.lastChild);
        };
      });
    };
    // 清空页面选中文字
    const emptySelectText = () => {
      window.getSelection
        ? window.getSelection().removeAllRanges()
        : document.selection.empty();
    };

    // 触发执行关键词查询（每次查询初始化，再执行新的查询）
    const startQuery = () => {
      data.textCenter = data.centerEle.innerText; //除去标签的内容(高亮段落使用)
      const absQueryKeyWord = data.queryKeyWord.trim(); //关键词去除前后空格
      if (absQueryKeyWord) {
        // 有搜索内容
        data.queryKeyWordArr = absQueryKeyWord
          .split(' ')
          .filter((item) => item.length); //获取关键词数组，删除空字符串（不然会卡死）
        data.queryKeyWordArr.sort((a, b) => b.length - a.length); // 字符长度降序 [北 , 北京]=> [北京 , 北]
        data.queryKeyWordReg = data.queryKeyWordArr.join('|'); //数组，再以 | 拼接（正则查询格式）
        // 查询正则和类型都没变化就不执行新检索操作
        if (
          data.oldQueryKeyWordReg === data.queryKeyWordReg &&
          data.oldQueryType === data.queryType
        )
          return;
        // 执行查询前
        data.oldQueryType = data.queryType; //备份此次搜索类型
        data.oldQueryKeyWordReg = data.queryKeyWordReg; //备份此次搜索词
        data.isLoading = true; //等待提示
        resetData('haveQuery'); //内容不同 ,有高亮重置
        setTimeout(() => {
          console.time('检索时间');
          setHighlight(); // 执行高亮逻辑,放在定时器才能显示loading
          console.timeEnd('检索时间');
        }, 0);
      } else {
        // data.isLoading = true; //等待提示
        data.oldQueryKeyWordReg = ''; //无内容搜索就设置空
        resetData(); //无内容  有高亮重置
      }
    };
    // 重置关键词（1）重置高亮重置
    const resetData = (isQuery) => {
      if (data.isHaveKey) {
        setTimeout(() => {
          console.time('重置时间');
          emptyAllHigh(); //清除全部高亮   速度慢
          console.timeEnd('重置时间');
          data.oldKeyEle = null; //清除保存旧的高亮元素
          data.oldPEle = null; //点击高亮元素
          data.paragraphClickHigh = []; //保存同段dom
          data.catalogue = []; //重置关键词段落数组
          data.catalogueP = []; //重置同篇段落目录
          data.keyWordIndex = []; //重置关键词下标
          data.Knum = 0; //重置当前高亮个数
          data.Ktotal = 0; //重置当前关键词总个数
          data.listNumId = 0; //重置列表计数
          data.isHaveKey = false; //重置无高亮词
          if (isQuery) return;
          // 无关键词关闭等待
          data.isLoading = false; //等待提示
        }, 0);
      }
    };
    //重置关键词（2）（速度优化） 清除长文本中所有高亮关键词
    const emptyAllHigh = () => {
      // 找到有高亮内容的外层包裹标签,修改innerHTML
      // let temporarySpanAll = Array.from(
      //   document.getElementsByClassName('temporarySpan')
      // );
      // temporarySpanAll.forEach((itemNode) => {
      // 最佳清空方式：k速度优化++ 7196个标签 308.1728515625 ms 循环所有的临时temporarySpan，替换内容中class="swhKey"高亮标签（这样不用重置table复制）
      //  直接替换innerHTML产生bug
      //   itemNode.parentNode.innerHTML = itemNode.innerHTML.replace(
      //   /<span class="swhKey"(?:.*?)>([\s\S]*?)<\/span>/g,
      //   '$1'
      // );
      // 下面上面均可(增加节点产生bug)
      // itemNode.parentNode.replaceChild(
      //   document.createTextNode(
      //     itemNode.innerHTML.replace(
      //       /<span class="swhKey"(?:.*?)>([\s\S]*?)<\/span>/g,
      //       '$1'
      //     )
      //   ),
      //   itemNode
      // );
      //  优化前方式1：使用节点替换节点内容（最慢）7196个标签 10559.537841796875 ms
      // itemNode.parentNode.replaceChild(
      //   document.createTextNode(itemNode.innerText),
      //   itemNode
      // );
      // });
      //  优化前方式2：直接替换整个文本，速度快，但是需要重置table复制 7196个标签 222.956787109375 ms
      data.centerEle.innerHTML = data.longTextContent; //重置内容（上面清除全部高亮）
      copyTable(); //重置表格复制按键
      // 优化前方式3：倒找高亮标签只删除标签上的高亮属性（不可理，会残留很多无用标签）
    };
    //（长文本）点击切换当前高亮关键词（上下切换关键词）
    const switchHighlight = (type, index) => {
      // 总数小于1不可点击切换
      if (data.Ktotal < 1) return;
      // 上下切换逻辑
      if (type === 'next') {
        //下一个
        data.Knum = data.Knum === data.Ktotal ? 1 : ++data.Knum;
      } else if (type === 'prev') {
        //上一个
        data.Knum = data.Knum <= 1 ? data.Ktotal : --data.Knum;
      } else {
        //点击整段获取首个子元素，点击高亮直接获取本身
        const targetEle = type.target.children[0] || type.target;
        // 通过id截取当前个数转数字类型，便于计算
        data.Knum = parseInt(targetEle.id.split('_')[1]);
        // 点击切换（同段同句 和 同篇）背景高亮 控制（注释即关闭）
        if (!data.catalogueP.length) {
          clickPHigh(index);
        } else {
          // 同篇截取字段背景高亮(注释后，取消同篇时点击列表，长文本中背景高亮功能)
          PieceLongHigh(type);
        }
      }
      // 通过id切换高亮和跳转位置
      clickPieceList();
    };
    // 同篇点击截取字符背景高亮
    const PieceLongHigh = (type) => {
      // 点击同篇长文本背景色
      setTimeout(() => {
        emptyPieceLongHigh();
        // 点击列表每一条时，获取li的元素，点击高亮子级就获取父级节点(解决点击列表高词有问题，待优化)
        const targetLi = type.target.children.length
          ? type.target
          : type.target.parentNode;
        //左侧列表长度
        const listLength = targetLi.innerText.length;
        // 当前关键词，第一个或者第二个节点的text  开头关键词就是 第一个（0）节点否则第二个节点（已解决bug 先0 后1）
        const keyText =
          targetLi.childNodes[0].innerText || targetLi.childNodes[1].innerText;
        // 关键词前面字符个数（即为列表中关键词开始位置）
        const listStart = targetLi.innerText.indexOf(keyText);
        // 关键词后面字符个数（列表长度减去关键词开始位置）
        const listEnd = listLength - listStart;
        // ****下面开始计算长文本中，开始和结束截取
        // 获取长文本中目标元素
        const targetEle = document.querySelector(`#key_${data.Knum}`);
        // 对应id位置,用于计算长文本中高亮段位置
        const keyIndex = targetEle.parentNode.innerHTML.indexOf(
          `key_${data.Knum}`
        );
        // 下面计算在长文本中以唯一id位置计算开始和结束截取位置
        const longStart = keyIndex - 25 - listStart;
        const longEnd = keyIndex + 89 + `${data.Knum}`.length + listEnd;
        // 截取长文本中目标字段
        const targetText = targetEle.parentNode.innerHTML.slice(
          longStart,
          longEnd
        );
        // 将目标文字含标签替换
        targetEle.parentNode.innerHTML = targetEle.parentNode.innerHTML.replace(
          targetText,
          `<span style="background-color:#ddd">${targetText}</span>`
        );
        const newTargetEle = document.querySelector(`#key_${data.Knum}`);
        data.oldTargetEle = newTargetEle.parentNode.parentNode;
        data.oldTargetText = targetText;
        data.oldNUm = data.Knum;
      }, 0);
    };
    // 清除同篇长文本截取段高亮
    const emptyPieceLongHigh = () => {
      if (data.oldTargetText) {
        // 标签文本替换纯文本
        data.oldTargetEle.innerHTML = data.oldTargetEle.innerHTML.replace(
          `<span style="background-color:#ddd">${data.oldTargetText}</span>`,
          data.oldTargetText
        );
        // 从新获取关键词元素，清除高亮样式
        const oldargetEle = document.querySelector(`#key_${data.oldNUm}`);
        // 如果页面存在当前关键词就清除样式，重新搜索时可能不存在当前id
        if (oldargetEle) {
          oldargetEle.style.backgroundColor = 'transparent'; // 清除背景色
          oldargetEle.style.color = data.keyWordBCol; // 重置字体高亮
        }
        data.oldTargetText = ''; //清除老的截取字段
      }
    };
    // 同段同句点击截取字符背景高亮
    const clickPHigh = (index) => {
      setTimeout(() => {
        let PEle;
        // 这里可以单独控制同段或同句截取段落是否有背景颜色
        if (data.paragraphClickHigh.length) {
          // 同段时找一级dom 高亮（保留下面一句，if直接删除）
          PEle = data.paragraphClickHigh[index];
        } else {
          // 同句时找temporarySpan 高亮（保留下面一句，if直接删除）
          PEle = document.getElementsByClassName('temporarySpan')[index];
        }
        // 清除之前元素的高亮
        if (data.oldPEle) {
          data.oldPEle.style.backgroundColor = 'transparent'; // 清除背景色
        }
        if (PEle) {
          PEle.style.backgroundColor = '#ddd';
          data.oldPEle = PEle;
        }
      }, 0);
    };
    //点击上下或者列表切换时都触发，清除截取字段背景色，清除上一个关键词高亮，设置当前关键词高亮
    const clickPieceList = () => {
      // 清除同段同句截取的高亮
      if (data.oldPEle) {
        data.oldPEle.style.backgroundColor = 'transparent'; // 清除背景色
      }
      // 清除同篇时截取字段背景高亮
      emptyPieceLongHigh();
      //  获取对应长文本中元素
      let keyEle = document.querySelector(`#key_${data.Knum}`);
      // 清除上一个元素的样式
      if (data.oldKeyEle) {
        data.oldKeyEle.style.backgroundColor = 'transparent'; // 清除背景色
        data.oldKeyEle.style.color = data.keyWordBCol; // 重置字体高亮
      }
      // 给当前元素增加样式
      keyEle.style.backgroundColor = data.keyWordBCol; // 设置背景高亮
      keyEle.style.color = data.keyWordSCol; // 设置当前字体白色
      // 定位到元素位置,过度
      // 方式1：不能设置具体位置 上中下（兼容问题）
      /*   keyEle.scrollIntoView({
        block: 'center', //定位在中间位置
        behavior: 'smooth' // 平滑过渡
      }); */
      // 方式2：可指定具体位置
      const wrapNode = document.querySelector('.long-text'); //内容外层容器
      // 移动高度
      wrapNode.scrollTo({
        top: trueOffsetTop(keyEle) - 400, //顶部移出去偏移量
        behavior: 'smooth'
      });
      // 保存当前元素,下次成为旧元素
      data.oldKeyEle = keyEle;
    };
    // 获取元素距离浏览器top高度
    const trueOffsetTop = (elm) => {
      let top = elm?.offsetTop;
      let parent = elm?.offsetParent; //最近的定位父元素
      while (parent !== null) {
        top += parent.offsetTop;
        parent = parent.offsetParent;
      }
      return top;
    };
    //（查询开始）（全部）点击查询开始执行（匹配关键词高亮&&关键词列表生成）
    const setHighlight = () => {
      // 匹配高亮之前进行过滤提高速度（同篇不是用forDomGetHighFilter 直接使用 forDomGetHigh 检索速度变慢）
      forDomGetHighFilter(data.centerEle);
      // 未检索出关键词
      if (data.Ktotal) {
        data.isHaveKey = true; //有高亮词
        // 有检索关键词再执行 段落列表获取
        console.time('列表时间');
        getQueryList();
        console.timeEnd('列表时间');
      } else {
        myTips('error', '文档中无符合的关键词~');
      }
      data.isLoading = false;
    };
    //（全部）（速度优化）高亮匹配之前 k速度优化++ 缩短一半速度，对所有的一级dom进行过滤，存在关键词的在进行递归替换
    const forDomGetHighFilter = (root) => {
      const children = root.children;
      const oneEleText = []; //保存包含关键词的第一层节点
      if (data.queryType === RETRIEVAL_TYPE.piece) {
        for (var i = 0; i < children.length; i++) {
          // 同篇逻辑  当前段落包含一个关键词就返回true
          const bool = data.queryKeyWordArr.some((item) => {
            return children[i].innerText.includes(item);
          });
          // 保存当前段落
          if (bool) {
            // 同篇按段分需要追加catalogueP（表格跨列分割）（catalogueP注释按全文截取1）
            // data.catalogueP.push({
            //   content: children[i].innerText
            // });
            // =====
            oneEleText.push(children[i]);
          }
        }
      } else if (
        data.queryType === RETRIEVAL_TYPE.paragraph ||
        RETRIEVAL_TYPE.sentence
      ) {
        for (var i = 0; i < children.length; i++) {
          // 同段逻辑  当前段落包含所有关键词才返回true
          const bool = data.queryKeyWordArr.every((item) => {
            return children[i].innerText.includes(item);
          });
          // 保存同段包含的元素
          if (bool) {
            // 同段k列表追加 保存同段文本,段落id和段落文本（之后处理内容高亮列表展示）
            if (data.queryType === RETRIEVAL_TYPE.paragraph) {
              data.paragraphClickHigh.push(children[i]);
              data.catalogue.push({
                content: children[i].innerText
              });
            }
            // 保存当前段落
            oneEleText.push(children[i]);
          }
        }
      }
      //对符合条件的节点进入递归 匹配高亮 （文章区域高亮）
      if (oneEleText.length) {
        oneEleText.forEach((item) => {
          forDomGetHigh(item);
        });
      }
    };

    //（长文本）（速度优化）***主要高亮逻辑***    传入dom元素，遍历节点，文本节点查找关键词替换高亮，标签节点递归
    const forDomGetHigh = (root) => {
      //===================  不需要频繁操作整个dom 速度明显提高(提升100倍速度  k速度优化++6000ms-60ms
      for (let i = 0; i < root.childNodes.length; i++) {
        // 当前节点
        const currentNode = root.childNodes[i];
        // 节点文本
        const nodeText = currentNode.nodeValue;
        // console.log(!nodeText.includes('\n'));
        if (currentNode.nodeType == 1) {
          //标签节点 直接队递归
          forDomGetHigh(currentNode);
        } else if (currentNode.nodeType == 3) {
          // 同句   空格\n停止,table中问题  data.queryType === RETRIEVAL_TYPE.sentence &&  !nodeText.includes('\n')
          //使用字符空格删除判断是否还有内容（阻止只有空格的内容）
          if (
            data.queryType === RETRIEVAL_TYPE.sentence &&
            nodeText.replace(/(^\s+)|(\s+$)|\s+/g, '')
          ) {
            // 执行同句高亮(当前节点 ， 节点内容)（上面判断去除空格，但是使用时还是带空格）
            withSentence(currentNode, nodeText);
            // 一定要return，不然会再次对加高亮标签的元素再次处理（导致bug）
            return;
          } else {
            // 同篇  按段分需要追加catalogueP（表格不跨列分割）（catalogueP注释按全文截取2）
            if (data.queryType === RETRIEVAL_TYPE.piece) {
              data.catalogueP.push({
                content: nodeText
              });
            }
            // 同段 同篇
            // 无论文本中是否包含关键词都会执行替换（优化  可先判断是否包含再替换  感觉是一样的，replace也是判断包含再替换）
            // 直接段落节点替换
            const highKeyStr = nodeTextHigh(nodeText);
            //嵌套标签需要下面判断，
            // *****判断后的值和之前的值是否一致，一致代表不需要更改，不一致代表有高亮关键词
            if (highKeyStr !== nodeText) {
              replaceNode(currentNode, highKeyStr);
            }
          }
        }
      }
      //===================

      //  以下是之前逻辑  只能捕获有标签的文本  不能匹配没有标签的文本（所以改成判断节点）
      //  操作dom过多查询慢
      /*  const children = root.children;
      // 同篇
      if(children.length){
        for (var i = 0; i < children.length;i++){
         children[i].children&&forDomGetHigh(children[i])
        }
      }else {
          // KW: 当前搜索的关键词本身
          // k1: 关键词查找的位置下标
          // 同段
          // 同篇查找高亮    多分支正则    证券|二次  关键词数组转为 | | 拼接data.queryKeyWordReg
          root.innerHTML= root.innerText.replace(new RegExp(data.queryKeyWordReg, "g"), (KW) => {
              //关键词下标保存
              data.Ktotal++;    //关键词总数量
              return `<span id="key_${data.Ktotal}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`;
          });
      } */
      // (每次查询都会重置整个文本html所以要从新创建复制按键，现在修改为删除之前高亮标签属性，不用再次设置表格)
    };
    //（长文本）同句单独逻辑
    const withSentence = (currentNode, nodeText) => {
      // 分割每一句  举例1: 搜索 孙 文 aaa孙a文aa。s孙a文hdddd;ccccc;hhhhs孙a文h
      const sentenceArr = nodeText.split(new RegExp('[。；;]')); //'[。；; \s]'  \s可以以空格分句
      // 获取过滤符合条件的数组  举例1: [aaa孙a文aa,s孙a文hdddd ,hhhhs孙a文h]
      const satisfySentenceArr = sentenceArr.filter((itemSentence) => {
        // 排除空字符串停止
        if (!itemSentence) return false;
        // 当前句子是否包含所有关键词
        const bool = data.queryKeyWordArr.every((item) => {
          return itemSentence.includes(item);
        });
        // 包含就返回
        if (bool) return itemSentence;
      });
      // 空数组就停止，不然或导致无匹配的句子也加航临时高亮标识
      if (!satisfySentenceArr.length) return;
      // 将符合条件的关键词高亮
      satisfySentenceArr.forEach((itemSen) => {
        // 同句k列表追加
        data.catalogue.push({
          content: itemSen
        });
        // 每一句高亮
        const highKeyStr = nodeTextHigh(itemSen);
        // 文本替换（整个节点高亮）
        // 每一句替换成高亮加外层包括temporarySpan
        nodeText = nodeText.replace(
          itemSen,
          `<span class="temporarySpan">${highKeyStr}</span>`
        );
      });
      //方式1： 直接改变innerHTML
      currentNode.parentNode.innerHTML = nodeText;
      //   方式2：替换节点，需要增加临时withSentence  span标签（重置时需要清除）
      // 再将整段临时创建外层标签withSentence包含所有同句
      // const withSentenceSpan = document.createElement('span');
      // withSentenceSpan.innerHTML = nodeText; //设置高亮内容
      // withSentenceSpan.className = 'withSentence'; //设置class(方便清除时使用)
      // // 替换当前节点内容（高亮节点，替换原始节点）
      // currentNode.parentNode.replaceChild(withSentenceSpan, currentNode);
    };
    //（长文本）节点文本高亮（用于长文本），后期可与列表文本组合
    const nodeTextHigh = (t) => {
      return t.replace(new RegExp(data.queryKeyWordReg, 'g'), (KW) => {
        data.Ktotal++; //替换次数（查找到的个数）
        // class="swhKey" 目前无用处（直接删除）
        return `<span class="swhKey" id="key_${data.Ktotal}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`;
      });
    };
    //（长文本） 高亮文本替换到节点中(同篇，同段使用)
    const replaceNode = (currentNode, highKeyStr) => {
      // 创建临时span包裹元素，设置内容，必须创建标签节点，文本节点不会以html形式展示<span>
      const temporarySpan = document.createElement('span');
      temporarySpan.innerHTML = highKeyStr; //设置高亮内容
      temporarySpan.className = 'temporarySpan'; //设置class(方便清除时使用)
      // 高亮文本替换之前文本，父节点执行替换（新节点，就节点）
      currentNode.parentNode.replaceChild(temporarySpan, currentNode);
      /**
       * 例如： <p>长文本渲染高亮替换逻辑</p>      查找的是 "渲染" 关键词
       * 处理后高亮：<p>
       *              替换以下整个包括元素（重置时直接删除temporarySpan）
       *             <span class="temporarySpan">
       *               "长文本"
       *              <span class="swhKey" id="key_1" style="color: rgb(255, 255, 255); padding: 0px 5px; background-color: red;">渲染</span>
       *               "高亮替换逻辑"
       *             </span>
       *            </p>
       *  */
    };

    //（列表）获取关键词段落列表（同篇 同段 同句）
    const getQueryList = () => {
      // 同篇段落获取
      if (data.queryType === RETRIEVAL_TYPE.piece) {
        // 有catalogueP按段分，没有按全篇
        data.catalogueP.length ? getOriginalPIndex() : getOriginalIndex();
      } else if (
        data.queryType === RETRIEVAL_TYPE.paragraph ||
        RETRIEVAL_TYPE.sentence
      ) {
        // 同段 同句 列表 处理段落时高亮
        data.catalogue.forEach((item) => {
          item.content = pureListTextHigh(item.content); //处理高亮
        });
      }
      // 搜索完成后定位至第一个关键词
      switchHighlight('next');
    };
    //同篇段落分割
    const getOriginalPIndex = () => {
      const regInter = eval(`/${data.queryKeyWordReg}/g`); //匹配正则
      // 循环段落
      data.catalogueP.forEach((p) => {
        const regExp = p.content.matchAll(regInter);
        const grouping = []; //每个段落下标单独存放
        for (const item of regExp) {
          // 保存每个关键词下标和关键词
          const keyPObj = {
            kWord: item[0], //查询的字符串
            index: item.index //查询的下标
          };
          // 每段追加到组数组
          grouping.push(keyPObj); //保存所有关键词下标
        }
        // 在追加到整个数组
        data.keyWordIndex.push(grouping);
      });
      // 循环获取每段开始结束位置(DIndex,替换每个段众多个关键词)
      data.keyWordIndex.forEach((group, DIndex) => {
        getHighIndex(group, DIndex);
      });
    };
    // 同篇 按全片分割列表
    //（列表）同篇 列表（1）  获取原始内容中关键词下标
    const getOriginalIndex = () => {
      // 去除标签内容获取关键词位置
      const regInter = eval(`/${data.queryKeyWordReg}/g`); //匹配正则
      const regExp = data.textCenter.matchAll(regInter); //匹配内容迭代器
      for (const item of regExp) {
        const keyPObj = {
          kWord: item[0], //查询的字符串
          index: item.index //查询的下标
        };
        data.keyWordIndex.push(keyPObj); //保存所有关键词下标
      }
      // 根据关键词下标，处理为前后30字符截取下标对象
      getHighIndex(data.keyWordIndex, 0);
    };
    //（列表）同篇 列表（2） 根据关键词下标，处理为前后30字符截取下标对象，生成关键词段落数组
    const getHighIndex = (KWArr, GIndex) => {
      let handleIndex = [];
      for (let i = 0; i < KWArr.length; i++) {
        // 前面位置，第一个就设置0开始，不是第一个就设置前面位置加字符长度
        const prevIndex = i
          ? KWArr[i - 1].index + KWArr[i - 1].kWord.length
          : 0;
        const currentIndex = KWArr[i].index; //当前关键词位置
        //下一个关键词位置,最后一次不存在undefined （不需要特殊判断，截取查出长度自动截取到最后）
        const nextIndex = KWArr[i + 1]?.index;
        //初始化开始截取位置，当前位置-30字符
        const initStartIndex = currentIndex - data.keyPLength;
        //初始化结束位置  ，当前位置+关键词长度+30字符
        const initEndIndex =
          currentIndex + KWArr[i].kWord.length + data.keyPLength;
        let handleIndexObj = {
          // 开始位置小于上一个关键词位置，就设置上个位置，否则设置当前开始位置（前面关键词停止）
          startIndex: initStartIndex < prevIndex ? prevIndex : initStartIndex,
          // 结束位置大于下一个关键词位置，就设置到关键词位置，否则就设置当前结束位置
          // （后面关键词停止）undefined时始终按结束位置，超出截取到最后
          endIndex: initEndIndex > nextIndex ? nextIndex : initEndIndex
        };
        handleIndex.push(handleIndexObj); // 开始结束位置对象
      }
      highParagraphHandle(handleIndex, GIndex); // 生成关键词段落数组
    };
    //（列表）同篇  按全片或段落   列表（3）  根据开始结束位置，生成关键词段落列表
    const highParagraphHandle = (SEindexObj, GIndex) => {
      SEindexObj.forEach((item, index) => {
        let pCenter = ''; //目录每段文字
        if (data.catalogueP.length) {
          // 有data.catalogueP代表按段分割
          pCenter = data.catalogueP[GIndex].content.slice(
            item.startIndex,
            item.endIndex
          );
        } else {
          //  全片分割
          //  截取关键词前后字段
          pCenter = data.textCenter.slice(item.startIndex, item.endIndex);
        }
        // 创建段落对象id和高亮内容
        const itemKeyWordObj = {
          content: pureListTextHigh(pCenter) //处理高亮
        };
        // 同篇k列表追加 保存关键词段落对象
        data.catalogue.push(itemKeyWordObj);
      });
    };
    //（列表）纯文本列表高亮（用于列表最后一步替每一条文本高亮）
    const pureListTextHigh = (itemContent) => {
      return itemContent.replace(
        new RegExp(data.queryKeyWordReg, 'g'),
        (KW, index) => {
          data.listNumId++;
          //  console.log(index);   这里获取同段中关键词位置（可以开发段落长度截取，不显示显示整个段落）
          return `<span id="list_${data.listNumId}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`; // 内容处理关键词高亮
        }
      );
    };
    // 提示浮窗
    const myTips = (type, text) => {
      proxy.h_tips({
        type: type, //success   error
        msg: text,
        top: 30,
        time: 1000
      });
    };
    return {
      data,
      startQuery,
      switchHighlight,
      choosefile
    };
  }
};
</script>

<style lang="less" scoped>
// loading动画
#LoadAnimation {
  margin: 250px auto;
  width: 50px;
  height: 50px;
  border: 2px white solid;
  border-left-color: #9c9c9c;
  border-right-color: #9c9c9c;
  border-radius: 100%;
  animation: loading1 0.5s infinite linear;
}
@keyframes loading1 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
// 搜索动画放大镜
.loading-icon {
  position: absolute;
  left: 48%;
  top: -22px;
  font-size: 40px;
  color: #9c9c9c;
}
.long-text-page {
  width: 1200px;
  margin: 0 auto;
  .doc-tit-box {
